<template>
    <div id="loader-wrapper" v-if="$store.state.first_load" :class="{loaded: loadStatus}">
        <div id="loader"></div>
        <div class="loader-section section-left"></div>
        <div class="loader-section section-right"></div>
        <div class="load_title">正在 💪 加载ing</div>
    </div>
</template>

<script>
"use strict";
export default {
    props: ["loadStatus"],
    mounted() {
        document.body.style.overflow = 'hidden';
        // alert(document.createElement('canvas').toDataURL('image/webp'))
    },
    data() {
        return {}
    },
    watch: {
        loadStatus(val) {
            if (val) {
                document.body.style.overflow = 'auto';
                //动画结束后。更改首次加载状态
                setTimeout(() => {
                    this.$store.commit('loadChange');
                },1000)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
$activeColor: #1ed9be;
#loader-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index:999999;
    width: 100%;
    height: 100%;
    .loader-section {
        position: fixed;
        top: 0;
        width: 51%;
        height: 100%;
        background: linear-gradient(#1ed9be, #cecec4);
        z-index: 1000;
        -webkit-transform: translateX(0);  /* Chrome, Opera 15+, Safari 3.1+ */
        -ms-transform: translateX(0);  /* IE 9 */
        transform: translateX(0);  /* Firefox 16+, IE 10+, Opera */
        -webkit-transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1.000);  
        transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1.000);
        &.section-left {left: 0;}
        &.section-right {right: 0;}
    }
    .load_title {
        position: absolute; 
        top: 62%;
        color: #FFF; 
        font-size: 16px; 
        width: 100%; 
        line-height: 30px;
        text-align: center; 
        z-index: 9999999999999;  
        opacity: 0.7;
    }
    &.loaded {
        visibility: hidden;
        -webkit-transform: translateY(-100%);  /* Chrome, Opera 15+, Safari 3.1+ */
        -ms-transform: translateY(-100%);  /* IE 9 */
        transform: translateY(-100%);  /* Firefox 16+, IE 10+, Opera */
        -webkit-transition: all 0.3s 1s ease-out;  
        transition: all 0.3s 1s ease-out;
        .loader-section.section-left {
            -webkit-transform: translateX(-100%);  /* Chrome, Opera 15+, Safari 3.1+ */
            -ms-transform: translateX(-100%);  /* IE 9 */
            transform: translateX(-100%);  /* Firefox 16+, IE 10+, Opera */
        }
        .loader-section.section-right {
            -webkit-transform: translateX(100%);  /* Chrome, Opera 15+, Safari 3.1+ */
            -ms-transform: translateX(100%);  /* IE 9 */
            transform: translateX(100%);  /* Firefox 16+, IE 10+, Opera */
        }
        .load_title {
            display: none;
        }
    }
}
#loader {
    display: block;
    position: relative;
    left: 50%;
    top: 50%;
    z-index:1001;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    border: 3px solid transparent;
    /* COLOR 1 */
    border-top-color: #FFF;
    -webkit-animation: spin 2s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    -ms-animation: spin 2s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    -moz-animation: spin 2s linear infinite; /* Chrome, Opera 15+, Safari 5+ */ 
    -o-animation: spin 2s linear infinite; /* Chrome, Opera 15+, Safari 5+ */   
    animation: spin 2s linear infinite; /* Chrome, Firefox 16+, IE 10+, Opera */
}
.loaded #loader {
    opacity: 0;
    -webkit-transition: all 0.3s ease-out;  
    transition: all 0.3s ease-out;
}
#loader:before {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    right: 5px;
    bottom: 5px;
    border-radius: 50%;
    border: 3px solid transparent;
    /* COLOR 2 */       
    border-top-color: #FFF;
    -webkit-animation: spin 3s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    -moz-animation: spin 3s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    -o-animation: spin 3s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    -ms-animation: spin 3s linear infinite; /* Chrome, Opera 15+, Safari 5+ */              
    animation: spin 3s linear infinite; /* Chrome, Firefox 16+, IE 10+, Opera */
}
#loader:after {
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #FFF;
    /* COLOR 3 */       
    -moz-animation: spin 1.5s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    -o-animation: spin 1.5s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    -ms-animation: spin 1.5s linear infinite; /* Chrome, Opera 15+, Safari 5+ */        
    -webkit-animation: spin 1.5s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    animation: spin 1.5s linear infinite; /* Chrome, Firefox 16+, IE 10+, Opera */
}
@-webkit-keyframes spin {
    0%{ 
        -webkit-transform: rotate(0deg);  /* Chrome, Opera 15+, Safari 3.1+ */
        -ms-transform: rotate(0deg);  /* IE 9 */
        transform: rotate(0deg);  /* Firefox 16+, IE 10+, Opera */
    }100%{
        -webkit-transform: rotate(360deg);  /* Chrome, Opera 15+, Safari 3.1+ */
        -ms-transform: rotate(360deg);  /* IE 9 */
        transform: rotate(360deg);  /* Firefox 16+, IE 10+, Opera */
    }
}

@keyframes spin {
    0%{ 
        -webkit-transform: rotate(0deg);  /* Chrome, Opera 15+, Safari 3.1+ */
        -ms-transform: rotate(0deg);  /* IE 9 */
        transform: rotate(0deg);  /* Firefox 16+, IE 10+, Opera */
    }100%{
        -webkit-transform: rotate(360deg);  /* Chrome, Opera 15+, Safari 3.1+ */
        -ms-transform: rotate(360deg);  /* IE 9 */
        transform: rotate(360deg);  /* Firefox 16+, IE 10+, Opera */
    }
}
</style>


